import { Collapse } from "antd";
import { CInput } from "@components";

const CFixAssetLease = ()=>{
    const {Panel} =  Collapse;

    return (
      <>
         <Collapse defaultActiveKey={["0"]} style={{ marginTop: 10 }}>
            <Panel header="Lease" key="0">
               <CInput
                  label="Lease ID"
                  name="lease_id"
                  id="lease_id"
                  autoFocus
                  isDisabled
               />

               <CInput
                  label="Lease Description"
                  name="lease_description"
                  id="lease_description"
                  autoFocus
                  isDisabled
               />
            </Panel>
         </Collapse>
      </>
    )
}

export default CFixAssetLease;